    content: "";
    position: absolute;
    width: 120px;
    height: 120px;
    animation: pulsate 2s infinite;
    border-radius: 50%;
    border: 5px solid rgba(25, 119, 204, 0.5);
    top: -15%;
    left: -15%;
    background: transparent;
}

.pulsating-play-btn .play-btn {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    font-size: 30px;
    color: white;
    text-align: center;
    z-index: 100;
    text-decoration: none;
}

@keyframes pulsate {
    0% {
